<template>
  <h1>登入成功！</h1>
  <p>稍等一下...這個頁面稍後會自行關閉...</p>
</template>

<script>
export default {
  name: 'Auth Login Success',
  mounted() {
    this.$route.hash.replace(/^#/, '').split('&').forEach(frac => {
      var items = frac.split('=')
      localStorage.setItem(items[0], items[1])
    })

    localStorage.setItem('expired_at', Date.now() + parseInt(localStorage.getItem('expires_in')) * 1000)

    localStorage.setItem('fetching_user', 'true')

    this.$http.post(process.env.VUE_APP_BACKEND_URL + "api/auth/user/", {
      access_token: localStorage.getItem('access_token'),
    }).then(response => {
      localStorage.setItem('user', response.data.user)
      localStorage.setItem('avatar', response.data.avatar)

      localStorage.setItem('fetching_user', 'false')
    })

    var timer = setInterval(() => {
      if (localStorage.getItem('fetching_user') == 'false') {
        clearInterval(timer)
        localStorage.removeItem('fetching_user')
        window.close()
      }
    }, 1000)

  },
}
</script>

